@charset "UTF-8";
:root {
  /**
  * Colours -- DARK
  */
  --dark__text-color: #ccc;
  --dark__bkg-color: #1c1c1c;
  /**
  * Colours -- LIGHT
  */
  --light__text-color: #222;
  --light__bkg-color: #fff;
  /**
  * Colours -- DEFAULT
  */
  --color__primary: #9a1818;
  --color__secondary: #ffc845;
  --color__link-body: var(--text-color);
  --color__link-body-underline: var(--text-color);
  --color__link-body-hover: var(--color__primary);
  --color__link-body-underline-hover: var(--color__secondary);
  --effect__transition: 0.4s ease-in-out;
  --font__body: "Inter", Arial, Helvetica, sans-serif;
}

html {
  font-size: 62.5%;
  line-height: 1.5;
  -webkit-text-size-adjust: 100%;
}

body {
  --text-color: var(--light__text-color);
  --bkg-color: var(--light__bkg-color);
}

body.dark-theme {
  --text-color: var(--dark__text-color);
  --bkg-color: var(--dark__bkg-color);
}

@media (prefers-color-scheme: dark) {
  /* defaults to dark theme */
  body {
    --text-color: var(--dark__text-color);
    --bkg-color: var(--dark__bkg-color);
  }

  body.light-theme {
    --text-color: var(--light__text-color);
    --bkg-color: var(--light__bkg-color);
  }
}
* {
  font-family: var(--font__body);
}

body {
  background: var(--bkg-color);
  font-size: clamp(1.8rem, 4vw, 2.5rem);
  color: var(--text-color);
  overflow-x: hidden;
}

main {
  display: grid;
  grid-template-columns: 1fr min(45ch, 100%) 1fr;
  max-width: 90%;
  margin: 0 auto 3em auto;
}
main > * {
  grid-column: 2;
}

a {
  color: var(--color__link-body);
  text-decoration-color: var(--color__link-body-underline);
  text-decoration-thickness: 0.125em;
  text-underline-offset: 0.25em;
  transition: var(--effect__transition);
}
a:hover, a:focus {
  color: var(--color__link-body-hover);
  text-decoration-color: var(--color__link-body-underline-hover);
}

a[href^="mailto:"]:after,
a[target=_blank]:after {
  display: inline-block;
  position: relative;
  top: 1px;
  left: 2px;
  content: "â†‘";
